import appCss from "../../../app.module.scss";
import catalogCss from "./catalog.module.scss"

import title from "../../../assets/images/pages/catalog/header/title.svg"
import {useMediaQuery} from "react-responsive";

const Catalog = () => {
    const isLess419 = useMediaQuery({query: `(max-width: 419px)`});
    const isLess600 = useMediaQuery({query: `(min-width: 420px) and (max-width: 599px)`});

    return <>
        <header>
            <div className={appCss.container}>
                <div className={appCss.content}>
                    <div className={catalogCss.wrapper}>
                        <div className={catalogCss.headerTitle}>Керамика</div>
                        <div className={catalogCss.headerDescription}>
                            Изучите нашу коллекцию товаров{isLess419 ? <br/> : " "}для{isLess600 ? <br/> : " "}дома,
                            изготовленных{isLess419 ?
                            <br/> : " "}в Японии. От
                            еды{isLess600 ? <br/> : " "}и керамики{isLess419 ? <br/> : " "}до
                            органических полотенец{isLess419 ? <br/> : isLess600 ? <br/> : " "}и японских ножей,
                            ведь
                            каждый{isLess419 ?
                            <br/> : " "}товар был{isLess600 ?
                            <br/> : " "}тщательно подобран{isLess419 ? <br/> : " "}для вас

                        </div>
                        <img src={title} alt={"title"}/>
                    </div>
                </div>
            </div>
        </header>
    </>
}

export default Catalog;
